export  class timer {
    constructor( loopFunc, interval = 1000 ) {
        this.interval = interval;
        this.timeHandler = null;
        this.loopFunc = loopFunc;
    }
    stop() {
        window.clearTimeout(this.timeHandler);
        this.timeHandler = null;
        this.loopFunc = null;
    }
    start() {
        if (this.timeHandler) {
            return;
        }

        const _task = () => {
            if (!this.loopFunc) {
                return;
            }

            this.timeHandler = window.setTimeout(() => {
                const triggered = this.loopFunc();

                // Normal function
                if (!triggered || !(triggered instanceof Promise) || !triggered.then) {
                    return _task();
                }

                // Promise
                triggered.then(() => {
                    _task();
                }).catch(() => {
                    _task();
                });
            }, this.interval);
        };
        
        _task();
    }
}

/**
 * promisepromise
 * @param {Function} createPromise promise
 * @param {Function} test return Boolean; promise{resolve,reject}booltrue
 * @param {Number} interval 
 * @param {Number} times 
 * @param {Number} timeout 
 */

export function doUntill({
    createPromise,
    test = ({
        resolve
    }) => resolve,
    interval = 3000,
    times = 100,
    timeout = 5 * 60 * 1000
}, ...args) {
    const that = this;
    const timesControl=times>0;
    const timeoutControl=timeout>0;
    return new Promise((res, rej) => {
        const initTime = Date.now();
        let t = 0;

        const tryAndTry = function () {
            t += 1;
            createPromise.call(that, ...args).then((result) => {
                if (test({
                    resolve: result
                })) {
                    return res(result);
                }
                setTimeout(tryAndTry, interval);
            }).catch(e => {
                if (test({
                    reject: e
                })) {
                    return rej(e);
                }
                
                if (timeoutControl&&(Date.now() - initTime) >= timeout || timesControl&&t >= times) {
                    return rej('');
                }
                setTimeout(tryAndTry, interval);
            });
        };
        tryAndTry();
    });
}